  },
  computed: {
    /**
     *  o   0~2, 3~5, 6~8 3 
     */
		resultType(){
      var result = 0,
          selectArrLength = this.$store.state.selectArr.length;

      if( 0 < selectArrLength && selectArrLength <= 2){
        result = 0
      } else if( 2 < selectArrLength && selectArrLength <= 5){
        result = 1
      } else if( 5 < selectArrLength && selectArrLength <= 8){
        result = 2
      }

			return result
		},
    resultList(){
      var result = [];
      var tmpDataList = [];
      var tmpTitleList = ['', '', '', ''];

      // Store       tmpDataList diseaseType    
      this.$store.state.selectArr.reduce((r, d) => {
        if(!r.includes(d.diseaseType)) {
          r.push(d.diseaseType)
        }
        return r;
      }, tmpDataList);

      // tmpDataList diseaseType 
      tmpDataList.sort();

      //        
      result = tmpDataList.reduce((r,d,i,t) => {
        r.push(
          // Object    assign  indexTitle, comma 
          Object.assign(
            this.productGroup[d], 
            {
              // t(tmpDataList)    indexTitle, comma 
              indexTitle: t.length > 1 ? tmpTitleList[i]:"", 
              comma:t.length > 1 ? "," : ""
            }
          )
        );
        return r;
      }, []);

      //        
      if(result.length == 0){
        result.push(
          // Object    assign  indexTitle, comma 
          Object.assign(
            this.productGroup[0], 
            {
              indexTitle: "", 
              comma: ""
            }
          )
        )
      }

      return result
    }
	},
  methods:{
    replaySurvey(){
      this.$router.replace('/');
      this.$store.commit('resetArr');
    },
    popupOpen(value){
      this.$store.commit('togglePopup', value);
    },
    onClickLink(value){
      console.log(value);
      window.open(value, "_blank");
